import debug from 'debug';
import { beforeMethod, afterMethod, afterInstance } from 'kaop-ts';
export function Logger(namespace) {
  return debug(['App', namespace].join('.'));
}
export const LogMethodAction = meta => {
  if (!meta.scope.log) return;
  const {
    method,
    args,
    result
  } = meta;
  meta.scope.log(method.name, args, result);
};
export const LogMethodProperty = property => meta => {
  if (!meta.scope.log) return;
  const {
    method,
    scope
  } = meta;
  meta.scope.log(method.name, property, scope);
};
export const Logs = afterInstance(meta => {
  const {
    method
  } = meta;
  meta.scope.log = Logger(method.name);
});
export const LogsAll = afterInstance(meta => {
  const target = meta.scope;
  const prototype = target.prototype;
  const wove = afterMethod(LogMethodAction);

  for (const key in prototype) {
    Object.defineProperty(prototype, key, wove(target, key, Object.getOwnPropertyDescriptor(prototype, key)));
  }
});
export const logsAfter = afterMethod(LogMethodAction);
export const logsBefore = beforeMethod(LogMethodAction);
export const logsPropertyAfter = property => afterMethod(LogMethodProperty(property));
export const logsPropertyBefore = property => beforeMethod(LogMethodProperty(property));